.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: burlywood;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* FONT STYLE FOR LARGE ITEMS*/
.Heading{
  font-family: Bradley Hand, cursive;
  font-size: 60px;
  text-align: center;
  width: 100%;
  padding: 1%;
}

/* ABOUT SECTION  */
.About{
  background-color: #997e5c;
  display: flex;
  flex-direction: row;
  padding: 10px;
  align-items: center;
}
.profile-picture{
  padding: 1%;
  height: 50%;
  width: 20%;
}
.profile{ 
  padding: 1%;
  background-color: #FFFFF2;
  height: min-content;
  border-radius: 10px;
}
.profile-title{
  font-size: 32px;
  font-weight: 500;
  font-style: italic;
}
.profile-text{
  font-size: 20px;
}

/* SPACER */
.Spacer{
  height: 200px;
}


.SWEN514{
  background-color: #997e5c;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 1%;
}  
.content-media{
  padding: 1%;
}
.content{ 
  padding: 1%;
  background-color: #FFFFF2;
  height: min-content;
  border-radius: 10px;
}
.content-title{
  font-size: 32px;
  font-weight: 500;
  font-style: italic;
}
.content-text{
  font-size: 20px;
}

.Spotify{
  background-color: #997e5c;
